.container {
  padding: 2rem;
  max-width: 1400px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
}

.title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
}

.subtitle {
  color: var(--muted);
}

.createBtn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  background: var(--primary);
  color: white;
  font-weight: 600;
  border: none;
  border-radius: 0.5rem;
  transition: all 0.2s ease;
}

.createBtn:hover {
  background: var(--primary-hover);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(124, 58, 237, 0.4);
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 1.5rem;
}

.card {
  background: var(--secondary);
  border: 1px solid var(--border);
  border-radius: 1rem;
  overflow: hidden;
  transition: all 0.2s ease;
}

.card:hover {
  border-color: var(--primary);
  transform: translateY(-4px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);
}

.cardThumbnail {
  height: 160px;
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  display: flex;
  align-items: center;
  justify-content: center;
}

.placeholderIcon {
  font-size: 3rem;
  opacity: 0.5;
}

.cardContent {
  padding: 1.25rem;
}

.cardTitle {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.cardDescription {
  color: var(--muted);
  font-size: 0.875rem;
  line-height: 1.4;
  margin-bottom: 1rem;
}

.cardMeta {
  display: flex;
  gap: 1rem;
}

.metaItem {
  font-size: 0.75rem;
  color: var(--muted);
}

.cardActions {
  display: flex;
  border-top: 1px solid var(--border);
}

.actionBtn {
  flex: 1;
  padding: 0.75rem;
  background: transparent;
  border: none;
  color: var(--muted);
  font-size: 0.875rem;
  transition: all 0.15s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.actionBtn:hover {
  background: rgba(255, 255, 255, 0.05);
  color: var(--foreground);
}

.actionBtn:not(:last-child) {
  border-right: 1px solid var(--border);
}

/* Add new template card */
.addCard {
  background: transparent;
  border: 2px dashed var(--border);
  border-radius: 1rem;
  min-height: 280px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  cursor: pointer;
}

.addCard:hover {
  border-color: var(--primary);
  background: rgba(124, 58, 237, 0.05);
}

.addCardContent {
  text-align: center;
  color: var(--muted);
}

.addIcon {
  display: block;
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.addHint {
  font-size: 0.75rem;
  margin-top: 0.5rem;
  max-width: 200px;
}
